.container {
    max-width: 1200px; /* Пример ограниченной ширины */
    margin: 0 auto;
    padding: 0 15px;
}

.header {
    display: flex;
    justify-content: space-between; /* Размещает элементы слева и справа */
    align-items: center; /* Вертикально центрирует элементы */
    padding: 10px 0;
}

.main {
    padding: 5px 0;
}

.header-left,
.header-right {
    display: flex;
    align-items: center; /* Вертикальное выравнивание внутри каждой части */
}

.header-right > * {
    margin-left: 15px; /* Пространство между элементами справа */
}

h2 {
    color: #ffffff; /* Белый цвет заголовков */
}

/* Новые стили для центрирования формы и стилизации элементов формы */
/* Общие стили для формы */
form {
    display: flex;
    flex-direction: column;
    align-items: center; /* Центрирование формы по горизонтали */
    justify-content: center; /* Центрирование формы по вертикали */
    margin-top: 10px; /* Отступ сверху */
}

/* Стили для текстовых полей и полей пароля */
form input[type="text"],
form input[type="password"],
form input[type="email"],
form input[type="NumberInput"],
form input[type="Number"],
form textarea,
form select {
    width: 100%; /* Ширина элементов формы */
    max-width: 300px; /* Максимальная ширина */
    padding: 10px;
    margin: 10px 0; /* Отступ между элементами */
    border-radius: 5px;
    border: 2px solid #333333;
    box-shadow: 0px 0px 10px 2px rgba(255, 223, 0, 0.7); /* Желтая тень */
    background-color: #333333;
    color: #ffffff;
    transition: box-shadow 0.3s ease;
}

/* Стили для кнопки */
form button {
    width: 100%; /* Ширина кнопки */
    max-width: 300px; /* Максимальная ширина */
    padding: 10px;
    margin: 10px 0; /* Отступ между кнопками */
    border-radius: 5px;
    border: 2px solid #333333;
    box-shadow: 0px 0px 10px 2px rgba(255, 223, 0, 0.7); /* Желтая тень */
    background-color: #333333;
    color: #ffffff;
    cursor: pointer; /* Курсор при наведении */
    transition: box-shadow 0.3s ease;
}

form button:hover {
    box-shadow: 0px 0px 15px 4px rgba(255, 223, 0, 1); /* Усиление тени при наведении */
}

/* Стили для текстовых областей */
form textarea {
    resize: vertical; /* Разрешить изменение размера по вертикали */
    min-height: 100px; /* Минимальная высота */
}

form select[multiple] {
    height: auto; /* Динамическая высота для множественного выбора */
    min-height: 100px; /* Минимальная высота для удобства */
    overflow: auto; /* Прокрутка, если слишком много элементов */
    background-color: #333333;
    color: #ffffff;
}

/* Дополнительные стили для опций */
form select option {
    background-color: #333333;
    color: #ffffff;
}

/* Стили для лейблов */
form label {
    color: #ffffff; /* Белый цвет текста */
    margin-bottom: 5px; /* Отступ под лейблом */
    transition: color 0.3s ease;
}

form label:hover {
    color: rgba(255, 223, 0, 1); /* Желтый цвет при наведении */
}

/* Стили для полей загрузки файлов */
form input[type="file"] {
    padding: 10px;
    border: 2px solid #333333;
    border-radius: 5px;
    background-color: #333333;
    color: #ffffff;
    transition: box-shadow 0.3s ease;
}

.Auth_title {
    color: rgba(255, 223, 0, 1); /* Желтый цвет текста */
    text-align: center; /* Центрирование текста */
    margin-bottom: 20px; /* Отступ снизу */
}

/* Контейнер для новостей */
.news-container {
    display: flex;
    flex-wrap: wrap; /* Позволяет элементам переноситься на новую строку */
    gap: 30px; /* Расстояние между элементами */
    justify-content: center; /* Центрирует элементы в контейнере */
}

/* Элемент новости */
.news-item {
    flex: 1 1 calc(25% - 30px); /* 4 элемента в ряд с учетом отступов */
    box-sizing: border-box; /* Учитывает отступы и границы в расчет ширины */
    max-width: calc(25% - 30px); /* Устанавливает максимальную ширину для адаптивности */
    margin-bottom: 16px; /* Отступ снизу для каждого элемента */
}

/* Стили для изображений новостей */
.news-image {
    width: 100%; /* Изображение занимает всю ширину контейнера */
    height: auto; /* Сохраняет пропорции изображения */
    display: block; /* Убирает отступы под изображением */
    border-radius: 8px; /* Закругление углов */
}

/* Стили для заголовков новостей */
.news-title {
    display: block;
    margin-top: 8px; /* Отступ сверху для заголовка */
    color: #ffffff; /* Цвет текста заголовка */
