.tooltip {
    position: relative;
    display: inline-block;
    cursor: pointer;
}

/* Скрытое всплывающее окно */
.tooltip .tooltiptext {
    visibility: hidden;
    width: 220px;
    background-color: #333333;
    color: #ffffff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 125%; /* Позиция всплывающего окна относительно родителя */
    left: 50%;
    margin-left: -110px; /* Половина ширины окна для центрирования */
    opacity: 0;
    transition: opacity 0.3s ease, visibility 0.3s ease; /* Плавное появление и скрытие */
}

/* Показать всплывающее окно при наведении */
.tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

#success-popup {
    display: none; /* Изначально скрываем всплывающее окно */
    position: fixed; /* Фиксированное позиционирование */
    top: 50%; /* Отступ сверху на 50% экрана */
    left: 50%; /* Отступ слева на 50% экрана */
    transform: translate(-50%, -50%); /* Центрирование по вертикали и горизонтали */
    padding: 20px; /* Внутренние отступы */
    background-color: #333; /* Фон всплывающего окна */
    color: #fff; /* Цвет текста */
    border-radius: 10px; /* Скругленные углы */
    box-shadow: 0px 0px 10px 2px rgba(255, 223, 0, 0.7); /* Желтая тень */
    z-index: 1000; /* Обеспечиваем, чтобы всплывающее окно было поверх остальных элементов */
    text-align: center; /* Выравнивание текста по центру */
}

/* Добавляем анимацию появления и скрытия */
#success-popup.fade-in {
    animation: fadeIn 0.5s forwards; /* Анимация появления */
}

#success-popup.fade-out {
    animation: fadeOut 0.5s forwards; /* Анимация исчезновения */
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }   
}

@keyframes fadeOut {
    from {
        opacity: 1;
    }
    to {
        opacity: 0;
    }
}


