/* Основной стиль для всплывающего окна и сообщений */
.popup, .alert {
    display: none; /* Изначально скрываем */
    position: fixed; /* Фиксированное позиционирование */
    top: 50%; /* Центрирование по вертикали */
    left: 50%; /* Центрирование по горизонтали */
    transform: translate(-50%, -50%); /* Центрирование по вертикали и горизонтали */
    padding: 20px; /* Внутренние отступы */
    background-color: #333; /* Фон окна */
    color: #fff; /* Цвет текста */
    border-radius: 10px; /* Скругленные углы */
    box-shadow: 0px 0px 10px 2px rgba(255, 223, 0, 0.7); /* Желтая тень */
    z-index: 1000; /* Поверх остальных элементов */
    text-align: center; /* Выравнивание текста по центру */
    max-width: 80%; /* Максимальная ширина */
    opacity: 0; /* Начальное значение opacity */
    transition: opacity 0.5s ease-in-out; /* Плавный переход для изменения прозрачности */
}

/* Появление окна */
.fade-in {
    opacity: 1;
}

/* Исчезновение окна */
.fade-out {
    opacity: 0;
}

/* Специфические стили для сообщений Django */
.alert {
    display: block; /* Видимый блок для сообщений */
}

/* Для успешных сообщений можно добавить цвет фона */
.alert.success {
    background-color: #333; /* Зеленый фон для успешных сообщений */
}

/* Для ошибок можно добавить другой цвет фона */
.alert.error {
    background-color: #333; /* Красный фон для ошибок */
}

/* Для информационных сообщений можно добавить другой цвет фона */
.alert.info {
    background-color: #333; /* Синий фон для информационных сообщений */
}

/* Стиль для кнопки закрытия */
.popup button, .alert button {
    background: #555; /* Фон кнопки */
    border: none; /* Без границы */
    color: #fff; /* Цвет текста кнопки */
    padding: 10px 20px; /* Внутренние отступы кнопки */
    border-radius: 5px; /* Скругленные углы кнопки */
    cursor: pointer; /* Указатель курсора */
    font-size: 16px; /* Размер шрифта кнопки */
    box-shadow: 0px 0px 10px 2px rgba(255, 223, 0, 0.7); /* Желтая тень */
    transition: box-shadow 0.3s ease, transform 0.3s ease; /* Плавные переходы */
}

/* Изменение фона кнопки при наведении */
.popup button:hover, .alert button:hover {
    background: #777; /* Измененный фон кнопки */
    box-shadow: 0px 0px 15px 4px rgba(255, 223, 0, 1); /* Более яркая тень при наведении */
    transform: scale(1.1); /* Увеличение при наведении */
}

.popup-content {
